/* eslint-disable no-undef */
const goalsAnalyticsHandler = () => {
  //     
  const messengers = document.querySelectorAll('.socials:not(.socials--footer) a.socials__link');
  messengers.forEach((messenger) => {
    messenger.addEventListener('click', () => {
      try {
        dataLayer.push({'event': 'goMessenger'});
      } catch (err) {
        // eslint-disable-next-line no-console
        console.warn('Google Tag Manager is not loaded');
      }
    });
  });

  //      
  const socialNetworks = document.querySelectorAll('.socials--footer a.socials__link');
  socialNetworks.forEach((socialNetwork) => {
    socialNetwork.addEventListener('click', () => {
      try {
        dataLayer.push({'event': 'goSocialNetwork'});
      } catch (err) {
        // eslint-disable-next-line no-console
        console.warn('Google Tag Manager is not loaded');
      }
    });
  });
};

export {goalsAnalyticsHandler};
