export default class PortfolioLoader {
  #container = null;
  constructor() {
    this.#container = document.querySelector('.modal-portfolio__loader');
  }

  hide = () => {
    this.#container.classList.remove('modal-portfolio__loader--show');
    this.#container.classList.remove('swiper-lazy-preloader');
    this.#container.classList.remove('swiper-lazy-preloader-white');
  }

  show = () => {
    this.#container.classList.add('modal-portfolio__loader--show');
    this.#container.classList.add('swiper-lazy-preloader');
    this.#container.classList.add('swiper-lazy-preloader-white');
  }
}
