export default class ViewNavigationButtons {
  #buttons = null;
  constructor(buttons) {
    this.#buttons = buttons;
  }

  hide = () => {
    this.#buttons.forEach((button) => {
      button.style.opacity = 0.1;
    });
  }

  show = () => {
    this.#buttons.forEach((button) => {
      button.style.opacity = '';
    });
  }
}
